let tabsParents = document.querySelectorAll("._tabs-parent")

tabsParents.forEach(tabsParent => {
    let tabs = tabsParent.querySelectorAll(".tab")
    tabs.forEach(tab => {
        tab.onclick = function () {
            let dataTab = this.getAttribute("data-tab")
            console.log(dataTab)
            removeActiveClasses(tabs)
            removeActiveClasses(tabsParent.querySelectorAll(".tab-content"))
            this.classList.add("_active")
            tabsParent.querySelector(`.${dataTab}`).classList.add("_active")
        }
    })
})

function removeActiveClasses(blocks) {
    blocks.forEach(block => {
        block.classList.remove("_active")
    })
}

