function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        autoplay: true,
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function aboutSlider() {
    var swiper = new Swiper('.about .swiper-container', {
        slidesPerView: 'auto',
        spaceBetween: 0,
        loop: true,
        pagination: {
            el: '.about .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function reviewSlider() {
    var swiper = new Swiper('.review .swiper-container', {
        slidesPerView: 'auto',
        spaceBetween: 0,
        navigation: {
            nextEl: '.review .swiper-button-next',
            prevEl: '.review .swiper-button-prev',
        },
    })
}

mainSlider()
aboutSlider()
reviewSlider()
